.card {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__img {
    width: 40px;
    height: 40px;
  }
  
  &__h {
    display: block;
    height: 60px;
    text-transform: uppercase;
    margin-top: 40px;

    font-family: "DINPro", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
  }
  
  &__text {
    font-size: 14px;
    line-height: 21px;
    margin-top: 20px;
    color: $text-color;
  }
}

@media (max-width: 767px) {
  .card:nth-child(n+2) {
    display: none;
  }
}
